import { Component } from '@angular/core';

import { Thin2Logger } from 'thin2-log';

import { Th2NovaTabModel } from '../../../utils/components/th2-nova-tabs/models/th2-nova-tab.model';

@Component({
    selector: 'tabs-view-component',
    templateUrl: './tabs-view.component.html'
})
export class TabsViewComponent {
    public tabsConfig: Th2NovaTabModel[] = [
        {
            id: 'view1',
            label: 'View 1',
            route: 'view1'
        },
        {
            id: 'view2',
            label: 'View 2',
            route: 'view2'
        },
        {
            id: 'view3',
            label: 'View 3',
            route: 'view3'
        },
        {
            id: 'lazy',
            label: 'Lazy',
            route: 'lazy'
        }
    ];

    constructor(private LOGGER: Thin2Logger) {}

    public onTabClick(tab: Th2NovaTabModel) {
        this.LOGGER.debug(`Tab ${tab.id} has been clicked!`);
    }
}
