import { NgModule } from '@angular/core';
import { Route, RouterModule } from '@angular/router';

import { TabsViewComponent } from './tabs-view.component';

import { View1Component } from './components/view-1.component';
import { View2Component } from './components/view-2.component';
import { View3Component } from './components/view-3.component';

const routes: Route[] = [
    {
        path: '',
        component: TabsViewComponent,
        children: [
            { path: 'view1', component: View1Component },
            { path: 'view2', component: View2Component },
            { path: 'view3', component: View3Component },
            { path: 'lazy', loadChildren: './lazy/lazy.module#LazyModule' },
            { path: '**', redirectTo: 'view1' }
        ]
    },
    { path: '**', redirectTo: '' }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class TabsViewRouting {}
